.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

/* body{
  align-items : center;
  background-color: #add7f6;
} */

html, body, body > div, .app{
  height:100%;
  width:100%;
}

.background-blue{
  background-color: #add7f6;
  width: 100%;
  height: 100%;
}

.Poll{
  text-align: center;
  color:white;
  background-color:#3B28CC;
  height: 8vmin;
  font-size: x-large;
  object-fit: cover;
  width: auto;
}

.PollResult{
  text-align: center;
  color:white;
  background-color:#3B28CC;
  height: 7vmin;
  font-size: x-large;
  object-fit: cover;
  width: auto;
  margin-top: 2rem;
}

.card{
  width: 18rem;
  margin-left: 10rem;
  display: flex;
  flex-wrap: "wrap";
  margin-top: 2 rem;
}

.column{
  float:left;
  width:25%;
  padding: 0 20px;
}

.img-cand{
  height:90px;
  width:90px;
}

.h5{
  marigin-bottom:none;
}

.winner{
  margin-left:10rem;
}

.fill img {
  
  width: 1688px;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.benefits{
  
  
}

.benefits img {
  border-style: solid;
  border-width: medium;
  margin-top: 1rem;
  margin-bottom: 1rem;
  width: auto;
}

.footer{
  background-color: #0D6EFD;
  color: white;
}

.Dashboard{
  margin-top: 2rem;
  margin-bottom: 2rem;
  text-align: center;
  color:white;
  background-color:#3B28CC;
  height: 8vmin;
  font-size: x-large;
  object-fit: cover;
  width: auto;
}

.text-center{
  text-align:center;
}

.Votecard{
  margin-bottom: 5rem;
  width: auto;
}
.col-md-3{
  margin-left: 4rem;
}

.form{
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  width: 50%;
  height:auto;
  margin: auto;
  
}


.form-label{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  bottom:none;
  top:100px;
  font-size: calc(10px + 2vmin);
}

.form-input{
  color: #eeeeee;
  padding-top: none;
  padding-bottom: 10px;
  padding-left: 2.5%;
  padding-right: 2.5%;
  font-size: 18px;
  font-family: initial;
}

.vote-button{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
  
}
.heading-title{
  text-align: center;
  margin-bottom: 2rem;
}
.admin-buttons{
  margin: 20px;
  
}

